import request from '@/utils/request'

// 管理员登录
export function login(data) {
    return request({
        url: '/manager/login',
        method: 'post',
        data
    })
}
// 管理员获取信息
export function getByToken() {
    return request({
        url: '/manager/getByToken',
        method: 'get',
        params: {},
        data: {}
    })
}

// 创建管理员
export function createManager(data) {
    const url = '/manager/manager/create'
    return request.post(url, data)
}
// 删除管理员
export function deleteManager(data) {
    console.log(data);
    return request({
        url: '/manager/manager/delete',
        method: 'delete',
        params: data
    })
}
// 修改管理员信息
export function updateManager(data) {
    const url = '/manager/manager/update'
    return request.put(url, data)
}

// 修改管理员密码
export function updateManagerPassword(data) {
    const url = '/manager/manager/update/password'
    return request.put(url, data)
}
// 重置管理员密码
export function manager_reset_password(data) {
    return request({
        url: '/manager/manager/resetPassword',
        method: 'get',
        params: data,
        data: {}
    })
}
// 获取管理员列表
export function findManager(data) {
    return request({
        url: '/manager/manager/find',
        method: 'get',
        params: data
    })
}

