

<template>
  <div class="container">
    <div class="main">
      <div class="input">
        <div class="title-container">
          <span class="title">管理员登录</span>
        </div>
        <div class="inof">
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="电话">
              <el-input v-model="form.account"></el-input>
            </el-form-item>
            <el-form-item label="密码">
              <el-input
                placeholder="请输入密码"
                v-model="form.password"
                show-password
              ></el-input>
            </el-form-item>

            <el-form-item v-if="isVerify">
              <img
                :src="changeImage"
                @click="handleClickImge"
                class="codeImage"
              />
            </el-form-item>
            <el-form-item label="验证码" v-if="isVerify">
              <el-input
                placeholder="请输入验证码"
                v-model="form.code"
              ></el-input>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="onSubmit">立即登录</el-button>
            </el-form-item>
          </el-form>
        </div>
        <!-- 注册 -->
      </div>
    </div>
  </div>
</template>
  
<script>
import { login, getByToken } from "@/api/manager/api.js";
import { getVerify } from "@/api/system/api.js";
import { store } from "@/utils/global.js";

export default {
  data() {
    return {
      store,
      isVerify: false,
      changeImage: "",
      form: {
        account: "",
        password: "",
      },
    };
  },
  methods: {
    getUserMessage() {
      getByToken().then((res) => {
        if (res.data.code != 200) {
          this.$message.error(res.data.msg);
        } else {
          sessionStorage.setItem("name", res.data.data.name);
          sessionStorage.setItem("image_url", res.data.data.image_url);
          sessionStorage.setItem("type", "manager");
        }
      });
    },
    // 获取验证码
    handleClickImge() {
      getVerify().then((res) => {
        this.changeImage = res.data.data.base64;
        this.form.verify_id = res.data.data.verify_id;
      });
    },
    onSubmit() {
      login(this.form).then((res) => {
        if (res.data.code != 200) {
          if (res.data.data >= 3) {
            `1`;
            this.isVerify = true;
            this.handleClickImge();
          } else {
            this.isVerify = false;
          }
          this.$message.error(res.data.msg);
        } else {
          sessionStorage.setItem("token", res.data.data.token);
          sessionStorage.setItem("user_id", res.data.data.user_id);
          this.getUserMessage();
          this.$message({
            showClose: true,
            message: "登录成功",
            type: "success",
          });
          setTimeout(() => {
            this.$router.push("/manager/home").catch(() => {});
          }, 100);
        }
      });
    },
  },
};
</script>
  
<style>
.container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  /* display: flex; */
  position: absolute;
  top: 50%;
  left: 50%;
  height: 66%;
  width: 27%;
  min-height: 300px;
  /* 设置最小高度 */
  min-width: 300px;
  /* 设置最小宽度 */

  transform: translate(-50%, -50%);
  box-shadow: 0 1px 12px #00000054;
}

.input {
  padding: 5%;
}

.inof {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 30%);
  padding: 5%;
}

.title-container {
  height: 50px;
}

.title {
  display: flex;
  /* position: relative; */
  justify-content: center;
  /* 水平居中 */
  align-items: center;
  /* 垂直居中 */

  font-weight: bolder;
  font-size: larger;
}
</style>
  